body{
    background-color: #fff;
}
@mixin  publics($h){
    width: 1000px;
    margin: 0 auto;
    height: $h;
}
tr,td,table,tbody{
    margin: 0;
    padding: 0;
}
.cartop{
    @include publics(50px);
    border-bottom: 2px solid #eee;
    p{
        width: 100px;
        height: 50px;
        line-height: 50px;
        color: #f40;
        font-size: 16px;
        text-indent: 10px;
        border-bottom: 2px solid #f40;
        float: left;
    }
    em{
        line-height: 50px;
        display: block;
        width: 150px;
        font-size: 12px;
        float: right;
        height: 50px;
        font-style: none;
        font-style: normal;
    }
    b{
        line-height: 50px;
        display: block;
        width: 50px;
        font-size: 12px;
        float: right;
        color: #f40;
        height: 50px;
    }
    input{
        width: 55px;
        height: 24px;
        background-color: gray;
        float: right;
        margin-top: 13px;
        color: #fff;
        border: none;
        outline: none;
        cursor: pointer;
    }
}
.table-top{
    @include publics(50px);
    table{
        width: 100%;
        height: 50px;
        tr{
            line-height: 50px;
            text-align: center;
            font-size: 13px;
            color: #3c3c3c;
            td:nth-child(1){
                width: 80px;
                input{
                    width: 15px;
                    height: 15px;
                    position: relative;
                    right: 10px;
                    top: 3px;
                    border: 1px solid #ccc;
                }
            }
            td:nth-child(2){
                width: 500px;
                text-align: left;
                text-indent: 20px;
            }
            td:nth-child(4){
                width: 120px;
            }
        }
    }
}
.table-center{
    @include publics(auto);
    margin-bottom: 20px;
    .store{
        line-height: 36px;
        height: 36px;
        font-size: 13px;
        input{
            width: 15px;
            height: 16px;
            border: 1px solid #ccc;
            position: relative;
            margin-right: 21px;
            position: relative;
            left: 11px;
            top:3px
        }
        span{
            margin-left: 10px;
        }
        img{
            width: 20px;
            height: 20px;
            position: relative;
            left: 20px;
            top:7px
        }
    }
    table{
        width: 1000px;
        height: auto;
        border: 1px solid #ccc;
        background-color: #fff8e1;
        tr{
            display: block;
            width: 1000px;
            height: 100px;
            border-bottom: 1px solid #ccc;
            margin-bottom: 20px;
            font-size: 12px;
            td:nth-child(1){
                width: 50px;
                input{
                    width: 15px;
                    height: 15px;
                }
            }
            td:nth-child(2){
                img{
                    width: 80px;
                    height: 80px;
                    position: relative;
                    top:7px;
                }
            }
            td:nth-child(3){
                padding-left: 5px;
                padding-right: 30px;
                width: 200px;
            }
            td:nth-child(4){
                padding-left: 5px;
                width: 200px;
                color: #9c9c9c;
            }
            td:nth-child(5){
                width: 100px;
                text-align: center;
                font-weight: bold;
            }
            td:nth-child(6){
                width: 120px;
                text-align: center;
                .right{
                    display: inline-block;
                    width: 20px;
                    height: 18px;
                    line-height: 20px;
                    border: 1px solid #ccc;
                    font-size: 14px;
                    cursor: pointer;
                    border-left: none;
                    position: relative;
                    top: 2px;
                    color: #999;
                    user-select: none;
                }
                .left{
                    display: inline-block;
                    width: 20px;
                    height: 18px;
                    border: 1px solid #ccc;
                    cursor: pointer;
                    font-size: 14px;
                    border-right: none;
                    position: relative;
                    top: 2px;
                    line-height: 20px;
                    color: #999;
                    user-select: none;
                }
                input{
                    width: 50px;
                    height: 18px;
                    border: 1px solid #ccc;
                    text-align: center;
                    outline: none;
                }
            }
            td:nth-child(7){
                text-align: center;
                width: 90px;
                font-weight: bold;
                color: red;
            }
            td:nth-child(8){
                text-align: center;
                width: 90px;
                cursor: pointer;
                user-select: none;
                &:hover{
                    color: red;
                }
            }
        }
        tr:last-child{
            margin-bottom: 0;
            border-bottom: none;
        }
    }
}
.bottom-wrap{
    @include publics(60px);
    margin-top: 50px;
    .car-bottom{
        width: 1005px;
        height: 60px;
        background-color: #e5e5e5;
        font-size: 13px;
        line-height: 60px;
        input{
            width: 15px;
            height: 15px;
            float: left;
            margin-top: 23px;
            margin-left:9px;
        }
        .all{
            margin-left: 15px;
            float: left;
            margin-right: 20px;
        }
        .del{
            position: relative;
            width: 26px;
            height:13px;
            cursor: pointer;
            user-select: none;
            &:hover{
                color: red;
            }
        }
        .car-bottom-right{
            width: 539px;
            float: right;
            height: 60px;
            position: relative;
            b{
                font-weight: normal;
                span{
                    color:#f40;
                    margin: 0 5px;
                    font-size: 18px;
                    font-weight: bold;
                }
            }
            .text{
                margin-left: 50px;
            }
            .total{
                display: inline-block;
                width: 200px;
                text-align: center;
                height: 60px;
                line-height: 60px;
                color:#f40;
                font-size: 18px;
            }
            .bn{
                display: inline-block;
                width: 100px;
                font-size: 18px;
                text-align: center;
                line-height: 60px;
                height: 60px;
                color: #fff;
                background-color: gray;
                cursor: pointer;
                position: absolute;
                right: 0;
            }
        }
    }
}
.end{
    @include public(60px);
    background-color: #eee;
    margin-bottom: 20px;
    text-align: center;
    margin-top: 150px;
    img{
        height: 40px;
        margin-top: 10px;
        display: inline-block;
    }
}